import { motion } from "framer-motion";
import styled from "styled-components";

export const ProjectsSection = styled.section`
  background: ${({ theme }) => theme.body};

  max-height: 1000vh;
  min-width: 100vw;
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-auto-flow: column;
  gap: 30px;
  padding: 70px 70px;
  position: relative;
  z-index: 4;
  transition: background 0.3s;

  .title {
    position: absolute;
    font-family: "Montserrat", sans-serif;
    font-size: 50px;
    top: 0;
    left: 70px;
    color: #162936;
  }
`;

export const Container = styled(motion.div)`
  background: white;
  color: #000;
  position: relative;
  display: block;
  border-radius: 10px;
  overflow: hidden;

  &::after {
    position: absolute;
    content: "Tap for more information";
    inset: 0;
    background-color: #000;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    transition: all 0.5s;
    opacity: 0;
  }

  .test {
    width: 100%;
    height: 100%;
    position: relative;
    display: block;
  }

  &:hover::after {
    opacity: 1;
    cursor: pointer;
  }
`;
